
#myHeader {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 1.5555555555555556rem;
  background: #CD3E3E;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
#menu,#search {
  width: 1.5555555555555556rem;
  height: 100%;
  text-align: center;
  line-height: 1.5555555555555556rem;
}
#menu i,#search i{
  color: #fff;
}
#routers {
  width: 4.666666666666667rem;
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
#routers div{
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
  line-height: 1.5555555555555556rem;
}
#routers div a{
  display: block;
  width: 100%;
  height: 100%;
  color: #e88889;
}
#routers div a.active{
  color: #fff;
}
#myHeader i{
  font-size: 0.6666666666666666rem;
}



