
#footer{
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 1.3611111111111112rem;
  background: rgba(255,255,255,0.8);
}
#footer img{
  position: absolute;
  top: 0.125rem;
  left: 0.20833333333333334rem;
  width: 1.125rem;
  height: 1.125rem;
}
#songInfo{
  position: absolute;
  top: 0.2638888888888889rem;
  left: 1.5416666666666667rem;
}
#songInfo #songName{
  color: #2b2b2b;
  font-size: 0.3888888888888889rem;
  /*margin-bottom: 0.1388888888888889rem;*/
}
#songInfo #artist{
  font-size: 0.3055555555555556rem;
  color: #959595;
}
#controller{
  position: absolute;
  top: 0;
  right: 0;
  width: 3.611111111111111rem;
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
#controller i{
  color: #cc3c3c;
}
#controller div{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
#controller div i{
  font-size: 0.4722222222222222rem;
}
#controller .menu, #controller .next{
  width: 1.1111111111111112rem;
  height: 100%;
}
#controller .play{
  width: 1.3888888888888888rem;
  height: 100%;
}
#controller .play i{
  font-size: 0.8333333333333334rem;
}
#progress{
  position: absolute;
  bottom: 0;
  left: 0;
  height: 0.05555555555555555rem;
  background: #cd3b3c;
}




